body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --brighte-color: #676aa6;
  --brighte-highlight: rgba(103, 106, 166, 0.4);
  --laleh-color: #a4814b;
  --laleh-highlight: rgba(164, 129, 75, 0.4);
  --lydi-color: #92c6e0;
  --lydi-highlight: rgba(146, 198, 224, 0.4);
  --nakoa-color: #b55038;
  --nakoa-highlight: rgba(181, 80, 56, 0.4);
  --rin-color: #275435;
  --rin-highlight: rgba(39, 84, 53, 0.4);
  --robyn-color: #5b7086;
  --robyn-highlight: rgba(91, 112, 134, 0.4);
  --sumina-color: #cc3d4b;
  --sumina-highlight: rgba(204, 61, 75, 0.4);

  --drach-color: #324a66;
  --drach-highlight: rgb(50, 74, 102, 0.4);
  --kayn-color: #69222d;
  --kayn-highlight: rgb(105, 34, 45, 0.4);
  --kiyone-color: #dbbea0;
  --kiyone-highlight: rgb(219, 190, 160, 0.4);
  --lirah-color: #545166;
  --lirah-highlight: rgb(84, 81, 102, 0.4);
  --misa-color: #8d3a54;
  --misa-highlight: rgb(141, 58, 84, 0.4);
  --rook-color: #577c77;
  --rook-highlight: rgb(87, 124, 119, 0.4);
  --taku-color: #5e4f4e;
  --taku-highlight: rgb(94, 79, 78, 0.4);
  --zeretta-color: #ae8fb0;
  --zeretta-highlight: rgb(174, 143, 176, 0.4);;
}

@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&display=swap');
