.details {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

.main_image {
    height: 100%;
    min-width: 75vh;
    margin-top: -85px;
}

.image {
    min-height: 120vh;
    min-width: 75vh;
}

.title_intro {
    color: #939494;
    font-size: 90%;
}

.title {
    font-size: 350%;
    font-family: 'Playfair Display', serif;
    line-height: 65%;
    margin-top: -10px;
}

.icon {
    width: 40px;
    height: 40px;
    padding: 10px;
    margin-top: 3.5%;
}

.excerpt {
    padding-left: 10%;
    padding-right: 10%;
    text-align: center;
    color: grey;
}

.body b {
    font-size: 140%;
}

.down_arrow, .up_arrow {
    width: 40px;
    height: 40px;

    margin-left: 48%;
    cursor: pointer;
}

.down_arrow {
    margin-top: 10px;
}

.up_arrow {
    margin-bottom: 5%;
}

.sector_down_arrow, .sector_up_arrow {
    width: 40px;
    height: 40px;

    margin-left: 70%;
    margin-top: -10%;
    cursor: pointer;
}

.sector_up_arrow {
    margin-top: 0px;
}

.arrow_instruct {
    color: #939494;
    font-size: 80%;
}

.sector_arrow_instruct {
    color: #939494;
    font-size: 80%;
    margin-left: 43%;
}

button, button:focus{
    outline: none;
    background: transparent;
    border: 1px solid transparent;
}
  
button:active {
    outline: none;
    background: transparent;
    border: 1px solid transparent;
}

.systemTitle {
    font-weight: bold;
    color: black;
}

.systemDetail {
    border-left: 2px solid black;
    padding-left: 10px;
}