.selectInstruct {
    display: flex;
    justify-content: center;
    font-size: 14px;
    color: #939494;
    margin-top: 30px;
}

.selection {
    &_chara {
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-flow: wrap;
        justify-content: center;
        padding-bottom: 100px;
        scroll-padding: 300px;

        &_container {
            width: 153.125px;
            height: 290px;
            margin: 7px;
            background-color: white;
            border-radius: 10px;
            border: 1px solid #d6d6d6;

            &:hover {
                cursor: pointer;
            }
        }

        &_name {
            height: 30px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 0 10px 0 10px;
            margin: 0px;

            color: white;
            background-color: rgba(62, 58, 58, 1);
            font-size: 14px;
            font-weight: bold;
            font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
            border-top-left-radius: 7px;
            border-top-right-radius: 7px;
        }

        &_tagline {
            height: 42.5px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 0 10px 0 10px;
            margin-top: -5px;

            color: white;
            line-height: 1;
            background-color: rgba(62, 58, 58, 1);
            font-size: 14px;
            font-style: italic;
            border-top: 1px solid black;
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
        }

        &_card {
            width: 153.125px;
            height: 216.5625px;
            border-top: solid black 1px;
            border-bottom: solid black 1px;
            background-size: 153.125px 216.5625px;
            -moz-transition: scaleX(-1);
            -webkit-transition: scaleX(-1);
            -o-transition: scaleX(-1);
            -ms-transition: scaleX(-1);
            transition: scaleX(-1);
            transition: all 0.2s ease-in-out;
            opacity: 1;
            filter: grayscale(10%);

            &_active {
                transition: fadein 3s;

                &:hover {
                    filter: brightness(110%);
                }
            }

            &_inactive {
                opacity: 0.5;
                transition: all 0.2s ease-in-out;
                filter: grayscale(100%);

                &:hover {
                    opacity: 0.7;
                    filter: grayscale(100%);
                }
            }
        }
    }
}

