.arrow,
.upArrow {
    display: flex;

    width: 35px;
    height: 30px;

    margin-left: calc(50% - 17.5px);
}

.arrow {
    margin-top: 70px;
}

.upArrow {
    transform: rotate(180deg);
    cursor: pointer;
}

.sArrow_instruct {
    color: #939494;
    font-size: 14px;
    margin-top: 15px;
    bottom: 0;
}

@keyframes shimmerCard {
    50% {
        -webkit-mask-position: left
    }

    100% {
        -webkit-mask-position: left
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes change {
    from {
        /*Use "to" to change the direction */
        background-position: right, center;
    }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}