.nav {
  width: 55%;
  display: inline;
  padding: 30px;

  font-size: 120%;
  margin-left: 30%;
  float: right;
  text-align: right;
  box-shadow: #939494;

  &_link {
    padding: 30px;
    color: #939494;
    transition: all 0.2s ease-in-out;

    &:hover {
      color: rgb(77, 77, 77);

      span {
        font-weight: bold;
      }
    }
  }
}

.active {
  color: black;
  font-weight: bold;
  text-decoration: underline !important;
  text-underline-offset: 10px;
  pointer-events: none;
}