.about_body {
    width: 100%;
    height: 100%;
    display: flex;
  
    font-size: 120%;
    font-family: Roboto, sans-serif;
    cursor: default;
}

.about_details {
    width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    padding-left: 15%;
    padding-right: 15%;
    text-align: center;
}

.about_title {
    font-size: 350%;
    font-family: 'Playfair Display', serif;
}

.creator {
    margin: 0px 30px 0px 30px;
    width: 42%;
}

.creator_section {
    display: flex;
    justify-content: center;
}

.creator_header {
    display: table;
    width: 100%;
    margin-bottom: -30px;
}

.creator_title {
    font-weight: bolder;
    float: left;
}

.creator_contacts {
    float: right;
}

.creator_description {
    border: 1px solid black;
    padding: 10px;
    width: 100%;
    height: 100px;
    display: inline-block;
}

li {
    width: 100%;
    text-align: left;
}

.creator_charas {
    width: 105%;
    height: 100px;
    text-align: left;
    margin: -5px 0px 10px 0px;
}

.creator_charas_icons {
    width: calc(105% / 8.9);
    height: auto;
    border: 1px solid black;
    margin-right: 10px;
}

.creator_charas_icon_last {
    width: calc(105% / 9);
    height: auto;
    border: 1px solid black;
}