.sector {
    width: 153.125px;
    height: 290px;
    margin: 7px;
    background-color: white;
    border-radius: 10px;
    border: 1px solid #d6d6d6;

    &_body {
        width: 100%;
        height: 100%;
        display: flex;
        background-color: #F4F4F3;
        font-size: 120%;
        font-family: Roboto, sans-serif;
        cursor: default;
        background: linear-gradient(135deg, rgb(229, 219, 216, 0.25) 25%, transparent 25%),
            linear-gradient(225deg, rgb(229, 219, 216, 0.25) 25%, transparent 25%),
            linear-gradient(315deg, rgb(229, 219, 216, 0.25) 25%, transparent 25%),
            linear-gradient(45deg, rgb(229, 219, 216, 0.25) 25%, transparent 25%);
        background-size: 50px 50px;
        background-attachment: fixed;

        -webkit-animation: fadein 5s;
        -moz-animation: fadein 5s;
        -ms-animation: fadein 5s;
        -o-animation: fadein 5s;
        animation: fadein 5s;
    }

    &_section {
        display: flex;
        flex-direction: column;
        align-items: center;
        /*
        background-image:
            linear-gradient(to right, rgb(229, 219, 216, 0.5) 1px, transparent 1px),
            linear-gradient(to bottom, rgb(229, 219, 216, 0.5) 1px, transparent 1px);
            */
    }

    &_title {
        max-height: 80vh;
        position: absolute;
        margin-top: 150px;
        font-size: 15vw;
        //font-size:calc(12px + 20vw);
        font-family: 'Playfair Display', serif;
        -webkit-text-fill-color: white;
        -webkit-text-stroke: 2px black;
        z-index: 2;
    }

    &_details {
        display: flex;
        justify-content: center;
        align-content: center;
        flex-direction: column;
        overflow: hidden;
    }

    &_splash {
        width: 100vw;
        position: relative;
        min-width: 1100px;
        --mask: linear-gradient(to bottom,
                rgba(0, 0, 0, 1) 0, rgba(0, 0, 0, 1) 70%,
                rgba(0, 0, 0, 0) 95%, rgba(0, 0, 0, 0) 0) 100% 50% / 100% 100% repeat-x;
        -webkit-mask: var(--mask);
        mask: var(--mask);
        z-index: 1;
        filter: blur(5px);

        &_main {
            filter: none;
        }
    }

    &_excerpt {
        width: 100%;
        margin: 0 10px 0 10px;
        padding: 20px;
        text-align: center;
        color: rgba(97, 97, 97, 1);
        background-color: rgba(255, 255, 255, 0.64);
        border: 1px solid rgba(97, 97, 97, 0.15);
        border-radius: 10px;
        z-index: 2;

        &_icon {
            height: 100px;
            padding: 20px;
        }

        &_container {
            width: 80vw;
            min-width: 800px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: -150px 0 100px 0;
            padding: 15px 5px 15px 5px;
            background-color: rgba(222, 215, 205, 0.5);
            border-radius: 10px;
        }
    }

    &_selection {
        &_icons {
            width: 150px;
            height: 150px;
            margin: 70px 10px 100px 10px;
            padding: 10px;

            &:hover {
                cursor: pointer;
            }
        }
    }
}

.places_section {
    &_wrapper {
        width: 100vw;
        height: 90vh;
        display: flex;
        flex-direction: row;
        justify-content: left;
        align-items: center;
        overflow: hidden;
    }

    &_mask {
        width: 500px;
        height: 500px;
        min-width: 500px;
        min-height: 500px;
        margin-left: -80px;
        border-radius: 500px;
        -webkit-border-radius: 500px;
        overflow: hidden;
        z-index: 2;
        border: 4px solid black;

        &_circle {
            &_outer {
                width: 560px;
                height: 560px;
                margin: -31px 0 0 -31px;
                position: absolute;
                background-color: rgba(222, 215, 205, 0.38);
                border: 2px dashed;
                border-color: #bf8fac transparent;
                border-radius: 50%;
                z-index: -3;
                animation: circle-stroke 15s ease infinite;
            }

            &_inner {
                width: 530px;
                height: 530px;
                margin: -20px 0 0 -20px;
                position: absolute;
                background-color: rgba(255, 255, 255, 0.64);
                border: 5px dotted;
                border-color: #252529 #bf8fac;
                border-radius: 50%;
                z-index: -3;
                animation: circle-rotate 120s linear infinite;
            }
        }
    }

    &_container {
        display: flex;

        &_img {
            width: 500px;
            height: 500px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-shrink: 0;
        }
    }

    &_info {
        width: calc(100vw - 540px);
        min-width: 600px;
        max-width: 800px;
        height: 120px;
        min-height: 120px;
        display: flex;
        align-items: center;
        padding: 20px;
        text-align: left;
        margin-top: 100px;

        &_name {
            width: 100%;
            position: absolute;
            left: 0;
            padding: 10px 0 10px 525px;
            margin-top: -320px;
            color: black;
            font-family: 'Playfair Display', serif;
            font-size: 300%;
            background-color: rgba(222, 215, 205, 0.5);
            z-index: 1;
        }

        &_nav {
            width: 100%;
            display: flex;
            justify-content: center;

            &_items {
                width: 70px;
                height: 70px;
                display: block;
                padding: 5px;
                background-color: rgba(222, 215, 205, 0.38);
                border-radius: 100%;
                background: #E0E0E0;
                margin: 6px;
                filter: grayscale(100%);

                &:hover {
                    filter: brightness(100%);
                    cursor: pointer;
                }

                &_active {
                    border: 1px solid black;
                    filter: none;

                    &:hover {
                        cursor: default;
                    }
                }
            }
        }
    }

    &_button {
        width: 70px;
        rotate: 270deg;
        z-index: 3;

        &:hover {
            cursor: pointer;
        }

        &_container {
            display: flex;
        }
    }
}

