@keyframes circle-stroke {
    0% {
        transform: rotate(180deg);
    }

    35% {
        transform: rotate(220deg);
    }

    70% {
        transform: rotate(140deg);
    }

    100% {
        transform: rotate(180deg);
    }
}

@keyframes circle-rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}