.App {
  text-decoration: none;
}

.slide-container {
  width: 75vh;
  height: 100%;
}

h3 {
  text-align: center;
}

.each-slide > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  height: 100vh;
}

.each-slide span {
  font-size: 20px;
  background: #efefef;
  text-align: center;
}

.each-fade {
  display: flex;
}

.each-fade .image-container {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.each-fade .image-container img {
  width: 100%;
  height: 100vh;
}

.lazy {
  width: 100%;
  min-height: 100vh;
  max-height: 100vh;
}

.indicator {
  position: relative;
  height: 15px;
  width: 15px;
  line-height: 50px;
  text-align: center;
  margin: 5px 20px 0px 20px;
  cursor: pointer;
  top: -197vh;
  color: transparent;
  z-index: 1000;
}

.indicator:before {
  position: absolute;
  content: '';
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background-color: white;
  transform: rotateX(45deg) rotateZ(45deg);
  border: 5px solid black;
  cursor: pointer;
}

.indicator.active:after {
  position: absolute;
  top: 7px;
  left: 7px;
  content: '';
  height: calc(100% - 4px);  
  width: calc(100% - 4px); 
  background-color: black;
  transform: rotateX(45deg) rotateZ(45deg);
  cursor: pointer;
}

.sector_img_caption {
  width: 100%;
  min-height: 18vh;
  padding: 10px 0px 10px 0px;
  background: rgba(0, 0, 0, 0.65);
  color: white;
  margin-top: -124.5vh;
}

.caption_padding {
  width: 80%;
  margin-left: 10%;;
}

.caption_title {
  font-size: 16px;
  font-family: 'Playfair Display', serif;
}

.caption_description {
  font-size: 15px;
  font-weight: normal;
}