.lazy {
  width: 100%;
  min-height: 100vh;
  max-height: 100vh;
}

.indicator {
  position: relative;
  height: 15px;
  width: 15px;
  line-height: 50px;
  text-align: center;
  margin: 5px 20px 0px 20px;
  cursor: pointer;
  top: -197vh;
  color: transparent;
  z-index: 1000;
}

.indicator:before {
  position: absolute;
  content: '';
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background-color: white;
  -webkit-transform: rotateX(45deg) rotateZ(45deg);
          transform: rotateX(45deg) rotateZ(45deg);
  border: 5px solid black;
  cursor: pointer;
}

.indicator.active:after {
  position: absolute;
  top: 7px;
  left: 7px;
  content: '';
  height: calc(100% - 4px);  
  width: calc(100% - 4px); 
  background-color: black;
  -webkit-transform: rotateX(45deg) rotateZ(45deg);
          transform: rotateX(45deg) rotateZ(45deg);
  cursor: pointer;
}

