.jokerIcon {
    width: 50px;
    height: 50px;
    margin: 20px;
    padding-left: 20px;
    top: 0;
    position: absolute;

    &:hover {
        cursor: pointer;
    }
}

.character {
    width: 153.125px;
    height: 290px;
    margin: 7px;
    background-color: white;
    border-radius: 10px;
    border: 1px solid #d6d6d6;

    &_body {
        width: 100%;
        height: 100%;
        display: flex;
        background-color: #F4F4F3;
        font-size: 120%;
        font-family: Roboto, sans-serif;
        cursor: default;
        background: linear-gradient(135deg, rgb(229, 219, 216, 0.25) 25%, transparent 25%),
            linear-gradient(225deg, rgb(229, 219, 216, 0.25) 25%, transparent 25%),
            linear-gradient(315deg, rgb(229, 219, 216, 0.25) 25%, transparent 25%),
            linear-gradient(45deg, rgb(229, 219, 216, 0.25) 25%, transparent 25%);
        background-size: 50px 50px;
        background-attachment: fixed;

        -webkit-animation: fadein 5s;
        -moz-animation: fadein 5s;
        -ms-animation: fadein 5s;
        -o-animation: fadein 5s;
        animation: fadein 5s;
    }

    &_section {
        display: flex;
        flex-direction: column;
        align-items: center;
        /*
        background-image:
            linear-gradient(to right, rgb(229, 219, 216, 0.5) 1px, transparent 1px),
            linear-gradient(to bottom, rgb(229, 219, 216, 0.5) 1px, transparent 1px);
            */

            .test {
                position: relative;
                display: inline-flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }
    }

    &_details {
        display: flex;
        justify-content: center;
        align-content: center;
        flex-direction: column;
        overflow: hidden;
    }

    &_splash {
        width: 100vw;
        min-width: 1100px;
        --mask: linear-gradient(to bottom,
                rgba(0, 0, 0, 1) 0, rgba(0, 0, 0, 1) 70%,
                rgba(0, 0, 0, 0) 95%, rgba(0, 0, 0, 0) 0) 100% 50% / 100% 100% repeat-x;
        -webkit-mask: var(--mask);
        mask: var(--mask);
        z-index: 1;
    }

    &_border {
        width: 100vw;
        min-width: 1100px;
        z-index: 2;
        margin-top: 28vw;
        position: absolute;
    }

    &_excerpt {
        width: 100%;
        margin: 0 10px 0 10px;
        padding: 20px;
        text-align: center;
        color: rgba(97, 97, 97, 1);
        background-color: rgba(255, 255, 255, 0.84);
        border: 1px solid rgba(97, 97, 97, 0.15);
        border-radius: 10px;
        z-index: 2;

        &_container {
            width: 60vw;
            min-width: 800px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: -180px 0 100px 0;
            padding: 15px 5px 15px 5px;
            background-color: rgba(220, 174, 152, 0.68);
            border-radius: 10px;
        }
    }

    &_story {
        display: flex;
        justify-content: center;

        &_selection {
            margin: 250px 10px 0 10px;

            &_icon {
                width: 200px;

            }

            &_title {
                margin: -10px 0 100px 0;
                font-size: 150%;
                font-family: 'Playfair Display', serif;
            }

            &_inactive {
                opacity: 0.25;
                transition: all 0.2s ease-in-out;

                &:hover {
                    opacity: 0.7;
                    cursor: pointer;
                }
            }
        }
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}